export const States = {

    /**Грыжа диска*/
    0: { id: 0, localName: 'Грыжа диска' },
    /**Крестцово-подвздошный сустав */
    1: { id: 1, localName: 'Крестцово-подвздошный сустав' },
    /**Фасеточный синдром */
    2: { id: 2, localName: 'Фасеточный синдром' },
    /**Биомеханические причины боли */
    3: { id: 3, localName: 'Биомеханические причины боли' },
    /**Миофасциальный синдром */
    4: { id: 4, localName: 'Миофасциальный синдром' },
    /**Стеноз позвоночного канала */
    5: { id: 5, localName: 'Стеноз позвоночного канала' },
    /**Грушевидный синдром */
    6: { id: 6, localName: 'Грушевидный синдром' },
    /**Протрузия */
    7: { id: 7, localName: 'Протрузия' },
    /**Метастaзы и другие страшные состояния, требующие неотложного визита к врачу */
    8: { id: 8, localName: 'Метастазы и другие страшные состояния, требующие неотложного визита к врачу' },
    /**Спондилолистез */
    9: { id: 9, localName: 'Спондилолистез' },
    /**Соматические расстройства */
    10: { id: 10, localName: 'Соматические расстройства' },
}


export function createWeights() {
    let weights = []

    for (const key in States) {
        weights.push({ state: States[key], value: 0 })
    }

    return weights
}

export default { States, createWeights }

