import React from 'react'
import NextButton from './NextButton'
import TextQuestion from './TextQuestion'
import ButtonsQuestion from './ButtonsQuestion'
import PicturesQuestion from './PicturesQuestion'

function Item(props) {

    const [activeAnswer, setActiveAnswer] = React.useState('')
    const [nextButtonState, setNextButtonState] = React.useState(false)

    function nextButtonHandler() {
        props.nextQuestionHandler({
            title: props.activeQuestion.title,
            answer: activeAnswer
        })
        setNextButtonState(false)
        setActiveAnswer('')
    }

    let currentItem = {}

    if (props.activeQuestion.type === 'text')
        currentItem = <TextQuestion
            activeQuestion={props.activeQuestion}
            setActiveAnswer={setActiveAnswer}
            setNextButtonState={setNextButtonState} />

    if (props.activeQuestion.type === 'buttons')
        currentItem = <ButtonsQuestion
            activeQuestion={props.activeQuestion}
            setActiveAnswer={setActiveAnswer}
            activeAnswer={activeAnswer}
            setNextButtonState={setNextButtonState} />
    if (props.activeQuestion.type === 'pictures') {
        currentItem = <PicturesQuestion 
        activeQuestion={props.activeQuestion}
        setActiveAnswer={setActiveAnswer}
        activeAnswer={activeAnswer}
        setNextButtonState={setNextButtonState} 
        />
            
    }

    if (props.activeQuestion === undefined)
        currentItem = 'Тест окончен!'

    return (
        <div>
            {currentItem}
            <NextButton nextButtonHandler={nextButtonHandler} nextButtonState={nextButtonState} />
        </div>
    )
}

