import React from 'react'

const divStyle = {
    display: 'inline-block',
    margin: '10px'
}

function PicturesQuestion(props) {

    function chooseVariantHandler(answer) {
        props.setActiveAnswer(answer)
        props.setNextButtonState(true)
    }

    return (
        <div className="m-3">
            <div>{props.activeQuestion.title}</div>
            {props.activeQuestion.answers.map((answer, index) => {
                return (
                    <div key={index}
                        style={divStyle}>
                        <img className="img-thumbnail"
                            src={answer.imageURL} alt='' width="200">
                        </img>
                        <div className="m-3 ">
                            <button
                                className={answer.title === props.activeAnswer.title ? "btn btn-primary" : "btn btn-secondary"}
                                onClick={() => chooseVariantHandler(answer)}>
                                {answer.title}
                            </button>
                        </div>
                    </div>
                )
            })}
        </div>
    )
}

export default PicturesQuestion