import React from 'react'

function Hello(props) {

    const [form, setForm] = React.useState({ email: '' })
    const [validationStatus, setValidationStatus] = React.useState({ emailHelpText: '' })

    function changeHandler(event) {
        setForm({ ...form, [event.target.name]: event.target.value })
    }

    function validateEmail(email) {
        return true
    }

    function onclickHandler(e) {
        e.preventDefault();
        let isValid = true
        let emailHelpText = ''
        if (!validateEmail(form.email)) {
            emailHelpText = 'Неверный email'
            isValid = false
        }

        setValidationStatus({ ...validationStatus, emailHelpText: emailHelpText, formValidationStatus: isValid })

        //save data
        if (isValid) props.saveHelloData(form)
    }


    return (
        <div>
            <div className="p-3">Приветствуем вас! Укажите ваши координаты, чтобы врач смог быстро найти ваш результат:</div>
            <form noValidate>
                <div className="mx-auto pb-3">
                    <label htmlFor="exampleInputEmail1" className="float-left">Имя</label>
                    <input type="email" className="form-control" name="firstName" aria-describedby="emailHelp" maxLength="100" onChange={changeHandler} />
                    <small id="emailHelp" className="form-text text-muted float-left"></small>
                </div>
                <div className="mx-auto pb-3">
                    <label htmlFor="exampleInputEmail1" className="float-left">Фамилия</label>
                    <input type="email" className="form-control" name="secondName" aria-describedby="emailHelp" maxLength="100" onChange={changeHandler} />
                    <small id="emailHelp" className="form-text text-muted float-left"></small>
                </div>
                <div className="mx-auto pb-4">
                    <label htmlFor="exampleInputEmail1" className="float-left">Email</label>
                    <input type="email" className="form-control " name="email" maxLength="200" required onChange={changeHandler} />
                    <small id="emailHelp" className="form-text text-muted float-left" >{validationStatus.emailHelpText}</small>
                    <div className="invalid-feedback">
                        Please choose a username.
                    </div>
                </div>
                <button type="submit" className="btn btn-primary float-left" onClick={onclickHandler}>Начать!</button>


                
            </form>
        </div>
    )
}

export default Hello