import React from 'react'

function TextQuestion(props) {

    function changeTextInputHandler(event) {
        props.setActiveAnswer(event.target.value)

        if (event.target.value !== '')
            props.setNextButtonState(true)
        else
            props.setNextButtonState(false)
    }

    return (
        <div className="m-3 w-75 mx-auto">
            <div className="m-3">{props.activeQuestion.title}</div>
            <textarea className="form-control"
                type="text"
                onChange={changeTextInputHandler}
            ></textarea>
        </div>
    )
}

export default TextQuestion