import React from 'react'

function ButtonsQuestion(props) {

    function chooseVariantHandler(answer) {
        props.setActiveAnswer(answer)
        props.setNextButtonState(true)
    }

    return (
        <div className="m-3">
            <div>{props.activeQuestion.title}</div>
            {props.activeQuestion.answers.map((answer, index) => {
                return (
                    <div key={index}
                        className="m-3 nav justify-content-center">
                        <button
                            className={answer.title === props.activeAnswer.title ? "btn btn-success w-75" : "btn btn-secondary w-75"}
                            onClick={() => chooseVariantHandler(answer)}>
                            {answer.title}
                        </button>
                        <br />
                    </div>
                )
            })}
        </div>
    )
}

export default ButtonsQuestion