import React from 'react'

function ProgressBar(props) {

    return (
        <div className="mx-auto px-3">
            <div className="text-left">{props.progressBarState.title}: ({(parseInt(props.progressBarState.done,10)+1)}/{props.progressBarState.total})</div>
            <div className="progress mx-auto">
                <div
                    className="progress-bar"
                    role="progressbar"
                    style={{ width: parseInt(((props.progressBarState.done / props.progressBarState.total) * 100), 10) + "%" }}
                    aria-valuemin="0"
                    aria-valuemax="100">
                </div>
            </div>
        </div>
    )
}

export default ProgressBar